.hero {
  background: #130d2c url("../img/new-page/hero-bg-desk.png") no-repeat 50% 0/cover;
  padding-top: 134px;
  position: relative;
}
.hero::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 377px;
  width: 100%;
  background: linear-gradient(180deg, rgba(19, 13, 44, 0) 0%, #130d2c 100%);
}
@media screen and (max-width: 1024px) {
  .hero {
    padding-top: 130px;
  }
}
.hero__container {
  display: flex;
  justify-content: space-between;
  gap: 59px;
}
.hero__info {
  width: 44%;
  position: relative;
  z-index: 1;
  margin-bottom: 130px;
}
.hero__info::before {
  content: "";
  position: absolute;
  bottom: -63px;
  right: -75px;
  width: 227px;
  height: 110px;
  background: url("../img/new-home-page/hero/plane-small.svg") no-repeat 0 0/cover;
}
.hero__title {
  margin-bottom: 30px;
  font-family: "Popins-semibold", sans-serif;
  font-size: 54px;
  line-height: 114%;
  background: linear-gradient(254deg, #fff 48.55%, #e6466e 72.99%, #e548ca 97%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.hero__sub-title {
  margin-bottom: 40px;
  color: rgba(255, 255, 255, 0.8);
  font-family: "Popins-regular", sans-serif;
  font-size: 16px;
  line-height: 150%;
}
.hero__play-btn {
  height: 66px;
  width: fit-content;
  padding: 0 42px;
  display: flex;
  gap: 18px;
  align-items: center;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0);
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #e5053d;
  box-shadow: 0px 2.732px 5.465px 0px rgba(255, 98, 65, 0.48);
  color: #fff;
  text-align: center;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.25);
  font-family: "Popins-bold", sans-serif;
  font-size: 20px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  transition: background-color 0.3s ease;
}
.hero__play-btn:hover {
  background-color: #b4012e;
}
.hero__button-arrow {
  width: 27px;
  height: 19px;
  padding-top: 2px;
}
.hero__devices {
  width: 50%;
  position: relative;
  z-index: 1;
}
.hero__devices::before {
  content: "";
  position: absolute;
  top: -36px;
  left: -97px;
  width: 366px;
  height: 134px;
  background: url("../img/new-home-page/hero/plane-big.svg") no-repeat 0 0/cover;
}
.hero__ipad {
  padding: 16px;
  transform: rotate(-13deg);
  margin-left: 20px;
  margin-top: 59px;
  width: 509px;
  height: 390px;
  background: url("../img/new-home-page/hero/ipad.png") no-repeat 0 0/cover;
  position: relative;
}
.hero__ipad::after {
  content: "";
  position: absolute;
  inset: 0;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.8);
  border-radius: 50px;
}
.hero__iphone {
  position: absolute;
  bottom: 0;
  right: -40px;
  background: url("../img/new-home-page/hero/iphone.png") no-repeat 0 0/cover;
  height: 485px;
  width: 276px;
}
.hero__marker {
  width: 320px;
  padding: 20px;
  border: 1px solid rgba(153, 153, 153, 0.2);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.084) 0%, rgba(217, 217, 217, 0.042) 100%);
  backdrop-filter: blur(25px);
  border-radius: 10px;
  position: absolute;
  display: flex;
  align-items: center;
  gap: 20px;
}
.hero .marker--ipad {
  bottom: 40px;
  left: 0px;
}
.hero .marker--iphone {
  right: -30px;
  bottom: 100px;
}
.hero .marker__icon {
  width: 60px;
  height: 60px;
  box-shadow: 0px 4px 8px -2px rgba(221, 0, 184, 0.48);
  border-radius: 50%;
}
.hero .marker__title {
  margin-bottom: 8px;
  color: #ffffff;
  font-family: "Popins-semibold", sans-serif;
  font-size: 24px;
  letter-spacing: 0.02em;
