.game {
  color: rgba(255, 255, 255, 0.8);
  background: linear-gradient(to bottom, #130d2c, #100c23);
}
.game__container {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-areas: "text image" "table image";
  gap: 20px;
  padding: 50px 15px 70px;
  position: relative;
}
.game__container::before {
  content: "";
  left: -120px;
  top: 50%;
  position: absolute;
  width: 241px;
  height: 241px;
  transform: translateY(-50%);
  border-radius: 50%;
  filter: blur(200px);
  background-color: #dd00b8;
  opacity: 0.5;
}
.game__item--1 {
  grid-area: text;
  margin-bottom: 50px;
}
.game__item--2 {
  grid-area: table;
}
.game__item--3 {
  position: relative;
  grid-area: image;
  width: 558px;
  height: 600px;
  align-self: center;
  border-radius: 12px;
  border: 1px solid #000;
  background: url("../img/new-home-page/game/frame.png") no-repeat 0 0/cover;
  backdrop-filter: blur(25px);
}
.game__phone {
  position: absolute;
  bottom: 1px;
  left: 50%;
  transform: translateX(-50%);
  width: 370px;
  height: 523px;
  background: url("../img/new-home-page/game/iPhone.png") no-repeat 0 0/cover;
  padding: 63px 18px 0;
  overflow: hidden;
}
.game__phone img {
  width: 100%;
  height: auto;
}
.game__phone::before {
  content: "";
  position: absolute;
  height: 180px;
  bottom: 0;
  width: 387px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(0deg, #1e1835 0%, rgba(30, 24, 53, 0) 100%);
}
.game__title {
  margin-bottom: 20px;
  font-family: "Popins-semibold", sans-serif;
  font-size: 40px;
  line-height: 130%;
  background: linear-gradient(254deg, #fff 48.55%, #e6466e 72.99%, #e548ca 97%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.game__sub-title {
  max-width: 552px;
  color: rgba(255, 255, 255, 0.8);
  font-family: "Popins-regular", sans-serif;
  font-size: 16px;
  line-height: 150%;
}
.game__table tbody {
  position: relative;
  display: block;
}
.game__table tbody::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 1px;
  width: 100%;
  background: linear-gradient(90deg, rgba(227, 44, 196, 0.8) 1.04%, rgba(227, 44, 196, 0) 100%);
}
.game__table-title {
  margin-bottom: 14px;
  color: #fff;
  font-family: "Popins-semibold", sans-serif;
  font-size: 20px;
  line-height: 130%;
  text-align: left;
}
.game__table-row {
  position: relative;
  display: block;
}
.game__table-row::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background: linear-gradient(90deg, rgba(227, 44, 196, 0.8) 1.04%, rgba(227, 44, 196, 0) 100%);
}
.game__table-info {
  padding: 10px 100px 10px 0;
  color: rgba(255, 255, 255, 0.6);
  font-family: "Popins-regular", sans-serif;
  font-size: 16px;
  line-height: 150%;
  display: inline-block;
  width: 30%;
}
.game__table-text {
  display: inline-block;
  width: 68%;
  color: #fff;
  font-family: "Popins-regular", sans-serif;
  font-size: 16px;
  line-height: 150%;
}

