.games {
  color: #E82052;
  background: linear-gradient(to bottom, #130d2c, #100c23);
}
.games__container {
  padding: 70px 15px;
  position: relative;
}
.games__title {
  margin-bottom: 20px;
  width: fit-content;
  font-family: "Popins-semibold", sans-serif;
  font-size: 40px;
  line-height: 130%;
  background: linear-gradient(254deg, #fff 48.55%, #e6466e 72.99%, #e548ca 97%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.games__description {
  color: rgba(255, 255, 255, 0.8);
  font-family: "Popins-regular", sans-serif;
  font-size: 16px;
  line-height: 150%;
  max-width: 700px;
}
.games__header {
  margin-bottom: 38px;
  display: flex;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
}
.games__buttons {
  display: flex;
  gap: 10px;
}
.games__button {
  width: 54px;
  height: 54px;
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #e5053d;
  box-shadow: 0px 4px 8px -2px rgba(255, 98, 65, 0.48);
  border-radius: 12px;
  transition: all 0.3s ease;
}
.games__button img {
  width: 19px;
  height: 26px;
}
.games__button--prew img {
  transform: rotate(-90deg);
}
.games__button--next img {
  transform: rotate(90deg);
}
.games__button:hover {
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #b4012e;
}
.games__button:disabled,
.games__button button[disabled] {
  cursor: no-drop;
  opacity: 0.5;
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #b4012e;
}
.games .game-item {
  position: relative;
  border-radius: 12px;
  border: 1px solid rgba(225, 32, 193, 0.4);
  box-shadow: 0px 4px 20px -1px rgba(0, 0, 0, 0);
  padding: 30px 20px 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 24px;
  overflow: hidden;
  box-shadow: 0px 4px 20px -1px rgba(0, 0, 0, 0);
}
.games .game-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 99px;
  height: 99px;
  border-radius: 50%;
  filter: blur(60px);
}
.games .game-item:nth-child(4n+1)::before {
  background-color: #20b520;
}
.games .game-item:nth-child(4n+2)::before {
  background-color: #a82393;
}
.games .game-item:nth-child(4n+3)::before {
  background-color: #c85f20;
}
.games .game-item:nth-child(4n+4)::before {
  background-color: #1d9edd;
}
.games .game-item::after {
  content: "";
  position: absolute;
  inset: 0;
  opacity: 0.2;
  background: #393737 0% 0%/100px 100px repeat;
  filter: blur(10px);
}
.games .game-item__marker {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 3px 8px;
  gap: 4px;
  border-radius: 1px 12px 1px 4px;
  background: #03b372;
}
.games .game-item__marker-ico {
  width: 18px;
  height: 18px;
}
.games .game-item__marker-text {
  color: #fff;
  text-align: center;
  font-family: "Popins-semibold", sans-serif;
  font-size: 12px;
  line-height: 110%;
  letter-spacing: 0.24px;
  text-transform: uppercase;
}
.games .game-item__avatar {
  width: 88px;
  height: 88px;
  align-self: center;
}
.games .game-item__title {
  text-align: center;
  color: #fff;
  font-family: "Popins-medium", sans-serif;
  font-size: 16px;
  line-height: 150%;
}
.games .game-item__rates {
  display: flex;
  gap: 4px;
}
.games .game-item__rate {
  position: relative;
  padding: 10px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  width: 33%;
  height: fit-content;
}
.games .game-item__rate:has(.game-item__rate-profit) {
  padding-bottom: 44px;
}
.games .game-item__rate-title {
  text-align: center;
  color: #fff;
  font-family: "Popins-medium", sans-serif;
  font-size: 24px;
  line-height: 150%;
}
.games .game-item__rate-text {
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  font-family: "Popins-regular", sans-serif;
  font-size: 12px;
  line-height: 150%;
}
.games .game-item__rate-profit {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #e120c1;
  color: #fff;
  padding: 2px 10px;
  text-align: center;
  font-family: "Popins-semibold", sans-serif;
  font-size: 12px;
  line-height: 110%;
  letter-spacing: 0.24px;
  text-transform: uppercase;
}
.games .game-item__sub-title {
  color: rgba(255, 255, 255, 0.8);
  font-family: "Popins-regular", sans-serif;
  font-size: 14px;
  line-height: 150%;
}
.games .swiper-pagination {
  display: none;
}
.games .swiper::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 54px;
  z-index: 1;
  background: linear-gradient(90deg, rgba(16, 12, 34, 0) 0%, #100c22 100%);
}

@media screen and (max-width: 1150px) {
  .games__container {
    padding: 40px 0;
  }
  .games__header {
    margin-bottom: 24px;
    padding: 0 14px;
  }
  .games__title {
    margin-bottom: 14px;
    text-align: center;
    font-size: 24px;
  }
  .games__description {
    font-size: 12px;
  }
  .games .swiper {
    padding: 0 0 40px;
  }
  .games .swiper-pagination-bullet {
    width: 0;
    height: 0;
    bottom: 0;
  }
}
@media screen and (max-width: 900px) {
  .games__title {
    font-size: 32px;
  }
  .games__description {
    font-size: 14px;
  }
  .games .game-item__title {
    font-size: 15px;
  }
  .games .game-item__rate-title {
    font-size: 22px;
  }
  .games .game-item__sub-title {
    font-size: 13px;
  }
}
@media screen and (max-width: 750px) {
  .games .swiper::after {
    display: none;
  }
}
@media screen and (max-width: 620px) {
  .games__header {
    margin-bottom: 24px;
  }
  .games__title {
    font-size: 24px;
  }
  .games__description {
    font-size: 12px;
  }
  .games .game-item__title {
    font-size: 14px;
  }
  .games .game-item__rate-title {
    font-size: 20px;
  }
  .games .game-item__sub-title {
    font-size: 12px;
  }
}
@media screen and (max-width: 620px) {
  .games__title {
    width: fit-content;
    margin: 0 auto;
  }
  .games__description {
    text-align: center;
  }
  .games__buttons {
    display: none;
  }
  .games .swiper-pagination {
    display: block;
  }
  .games .swiper-pagination-bullet {
    background-color: #e5053d;
    width: 12px;
    height: 12px;
    box-shadow: 0px 2.73239px 5.46479px rgba(255, 98, 65, 0.48);
    border-radius: 8px;
    transition: opacity 0.3s ease;
  }
  .games .swiper-pagination-bullet:hover {
    opacity: 0.8;
  }
}
.evening {
  position: relative;
  padding-top: 1px;
  background: #100c23 url("../img/new-home-page/evening/bg.jpg") no-repeat top/cover;
}
.evening__title {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 90px;
  z-index: 1;
  width: fit-content;
  margin: 0 auto;
  font-family: "Popins-semibold", sans-serif;
  font-size: 40px;
  line-height: 130%;
  background: linear-gradient(254deg, #fff 48.55%, #e6466e 72.99%, #e548ca 97%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  user-select: none;
  pointer-events: none;
}
.evening__sub-title {
  position: absolute;
  left: 50%;
  bottom: 30px;
  z-index: 1;
  transform: translateX(-50%);
  max-width: 555px;
  margin: 0 auto;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  font-family: "Popins-regular", sans-serif;
  font-size: 16px;
  line-height: 150%;
  user-select: none;
  pointer-events: none;
}
.evening__swiper {
  position: relative;
  margin-top: 50px;
}
.evening__swiper::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 250px;
  background: linear-gradient(180deg, rgba(19, 13, 44, 0) 0%, #130d2c 61.03%);
}
.evening .swiper {
  max-width: 1220px;
}
.evening .swiper-pagination {
  display: none;
}
.evening .swiper-wrapper {
  position: relative;
}
.evening .swiper-slide {
  margin-top: auto;
  transition: all 0.3s;
}
.evening__phones {
  height: 555px;
}
.evening__phone {
  max-width: 220px;
  max-height: 449px;
  background: url("../img/new-home-page/evening/phone.png") no-repeat 0 0/cover;
  position: relative;
  padding: 37px 12px;
}
.evening__phone::after {
  content: "";
  position: absolute;
  bottom: -5px;
  right: 0;
  left: 0;
  height: 200px;
  background: linear-gradient(180deg, rgba(19, 13, 44, 0) 0%, #130d2c 61.03%);
}
.evening .swiper-slide-active {
  margin-top: 0;
}
.evening .swiper-slide-prev {
  margin-top: auto;
  margin-bottom: auto;
}
.evening .swiper-slide-next {
  margin-top: auto;
  margin-bottom: auto;
}

@media (max-width: 1259px) {
  .evening__swiper {
    padding-top: 150px;
  }
  .evening__title {
    top: 0;
    bottom: auto;
  }
  .evening__sub-title {
    top: 50px;
    bottom: auto;
    width: 70%;
  }
  .evening .swiper {
    max-width: 700px;
  }
  .evening .swiper-wrapper {
    position: relative;
  }
  .evening .swiper-pagination {
    display: block;
    bottom: 80px;
  }
  .evening .swiper-pagination-bullet {
    background-color: #e5053d;
    width: 12px;
    height: 12px;
    transition: opacity 0.3s ease;
  }
  .evening .swiper-pagination-bullet:hover {
    opacity: 0.5;
  }
}
@media (max-width: 1050px) {
  .evening__title {
    margin-bottom: 18px;
    font-size: 32px;
  }
  .evening__sub-title {
    font-size: 14px;
  }
}
@media (max-width: 699px) {
  .evening {
    padding-top: 50px;
  }
  .evening::after {
    width: 100%;
  }
  .evening__phone {
    max-width: 150px;
    max-height: none;
  }
  .evening__swiper {
    margin-top: 0;
  }
  .evening .swiper-pagination {
    bottom: 5px;
  }
  .evening .swiper-slide {
    width: auto;
    max-width: none;
    transition: all 0.3s;
  }
  .evening .swiper-slide-active {
    margin-top: 0;
  }
  .evening .swiper-slide-prev {
    margin-top: 35px;
  }
  .evening .swiper-slide-next {
    margin-top: 35px;
  }
}
@media (max-width: 500px) {
  .evening {
    padding-top: 50px;
  }
  .evening::after {
    width: 300px;
    height: 300px;
  }
  .evening__swiper::after {
    height: 180px;
  }
  .evening__phones {
    height: 341px;
  }
  .evening__phone {
    max-width: 150px;
    max-height: 306px;
  }
  .evening .swiper-wrapper {
    position: relative;
  }
  .evening .swiper-slide {
    width: auto;
    max-width: none;
    transition: all 0.3s;
    min-height: 400px;
  }
  .evening .swiper-slide-active {
    margin-top: 0;
  }
  .evening .swiper-slide-prev {
    margin-top: 35px;
  }
  .evening .swiper-slide-next {
    margin-top: 35px;
  }
}
@media (max-width: 490px) {
  .evening__title {
    margin-bottom: 14px;
    font-size: 24px;
  }
  .evening__sub-title {
    width: 90%;
