  }
}
@media (max-width: 375px) {
  .evening__container {
    padding-left: 10px;
    padding-right: 10px;
  }
  .evening__title {
    width: 100%;
    text-align: center;
  }
}
.wins {
  background: linear-gradient(to bottom, #130d2c, #100c23);
  position: relative;
}
.wins__container {
  padding: 70px 15px;
  margin: 0 auto;
  display: flex;
  gap: 80px;
}
.wins__title {
  margin-bottom: 20px;
  width: fit-content;
  font-family: "Popins-semibold", sans-serif;
  font-size: 40px;
  line-height: 130%;
  background: linear-gradient(254deg, #fff 48.55%, #e6466e 72.99%, #e548ca 97%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.wins__sub-title {
  margin-bottom: 30px;
  color: rgba(255, 255, 255, 0.8);
  font-family: "Popins-regular", sans-serif;
  font-size: 16px;
  line-height: 150%;
}
.wins__left {
  width: 54%;
}
.wins__table-wrapper img {
  width: 100%;
}
.wins__buttons {
  margin-bottom: 40px;
  padding: 5px;
  display: flex;
  gap: 12px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: #0f0c22;
  box-shadow: 0px 2px 4px 0px rgba(255, 255, 255, 0.13) inset;
}
.wins__button {
  padding: 15px;
  width: 50%;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0);
  background-color: transparent;
  color: #fff;
  text-align: center;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.25);
  font-family: "Popins-bold", sans-serif;
  font-size: 20px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  transition: background-color 0.3s ease;
}
.wins__button:hover {
  border: 1px solid rgba(255, 255, 255, 0);
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.06) 0%, rgba(255, 255, 255, 0) 100%), rgba(229, 5, 61, 0.3);
}
.wins__button--active {
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #e5053d;
  box-shadow: 0px 4px 8px -2px rgba(255, 98, 65, 0.48);
}
.wins__button--active:hover {
  background: radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #e5053d;
  box-shadow: 0px 4px 8px -2px rgba(255, 98, 65, 0.48);
}
.wins__table {
  display: none;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: #0f0c22;
  box-shadow: 0px 2px 4px 0px rgba(255, 255, 255, 0.13) inset;
  padding: 0 40px;
  position: relative;
  max-height: 650px;
  overflow-y: auto;
  scrollbar-color: #e5053d transparent;
}
.wins__table::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 46px;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
}
.wins__table table {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.wins__table table thead {
  padding-left: 40px;
}
.wins__table table thead tr {
  display: flex;
  justify-content: space-between;
}
.wins__table table thead tr th {
  padding: 15px 5px 15px 0;
  color: rgba(255, 255, 255, 0.5);
  font-family: "Popins-semibold", sans-serif;
  font-size: 14px;
  text-align: start;
  width: fit-content;
}
.wins__table table thead tr th:first-child {
  width: 30%;
}
.wins__table table tbody {
  display: flex;
  flex-direction: column;
  position: relative;
}
.wins__table table tbody::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 1px;
  width: 100%;
  background: linear-gradient(90deg, rgba(227, 44, 196, 0.8) 1.04%, rgba(227, 44, 196, 0) 100%);
}
.wins__table table tbody tr {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.wins__table table tbody tr::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background: linear-gradient(90deg, rgba(227, 44, 196, 0.8) 1.04%, rgba(227, 44, 196, 0) 100%);
}
.wins__table table tbody tr td {
  padding: 20px 5px 20px 0;
  color: #fff;
  font-size: 14px;
  font-family: "Popins-semibold", sans-serif;
  vertical-align: middle;
}
.wins__table table tbody tr td img {
