  line-height: 150%;
}
.faq__question {
  border-radius: 12px;
  border: 1px solid rgba(225, 32, 193, 0.4);
}
.faq .question {
  position: relative;
  transition: all 0.3s ease;
  height: auto;
}
.faq .question::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.084) 0%, rgba(217, 217, 217, 0.042) 100%);
  backdrop-filter: blur(25px);
  border-radius: 12px;
}
