  position: relative;
  color: #fff;
  font-family: "Popins-semibold", sans-serif;
  font-size: 18px;
  line-height: 130%;
  padding-left: 22px;
  padding-right: 10px;
}
.faq .question__btn {
  position: relative;
  padding: 17.5px;
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  background: url("../img/new-home-page/faq/arrow-bg.png") no-repeat 50% 50%/contain;
}
.faq .question__btn::after {
  content: "";
  width: 19px;
  height: 19px;
  transition: all 0.3s ease;
  background: url("../img/new-home-page/faq/arrow.svg") no-repeat 50% 50%/contain;
}
.faq .question__btn:hover {
  background: url("../img/new-home-page/faq/arrow-bg-hover.png") no-repeat 50% 50%/contain;
}
.faq .question__btn.active {
  background: url("../img/new-home-page/faq/arrow-bg-active.png") no-repeat 50% 50%/contain;
}
.faq .question__btn.active::after {
  transform: rotate(180deg);
}
.faq .question__answer {
  position: relative;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  height: 0;
  overflow: hidden;
  transition: all 1s;
  opacity: 0;
}
.faq .question__answer.active {
  height: fit-content;
  opacity: 1;
}
.faq .question__text {
  padding: 20px 73px 32px 30px;
  color: rgba(255, 255, 255, 0.8);
