  &__showMoreBtn {
    display: none;
    position: relative;
    bottom: 0;
    left: 0;
    right: 0;
    color: #e5053d;
    text-align: center;
    font-family: $font-popins-bold;
    font-size: 20px;
    letter-spacing: 0.4px;
    text-transform: uppercase;
    position: relative;
    transition: all $transition;
    background-color: transparent;
    z-index: 2;
    &::after {
      content: '';
      display: inline-block;
      width: 18px;
      height: 13px;
      background-color: #e5053d;
      -webkit-mask: url('../img/new-page/header-menu-arrow.svg') no-repeat center;
      -webkit-mask-size: contain;
      mask: url('../img/new-page/header-menu-arrow.svg') no-repeat center;
      mask-size: contain;
      margin: 0 0 0 8px;
      transition: all $transition;
    }
    &:hover {
      color: #b4012e;
      &::after {
        background-color: #b4012e;
      }
    }
    &.visible {
      display: block;
      margin: 0 auto 20px;
    }
    &.less {
      position: relative;
      margin-top: 10px;
      &::after {
        transform: rotate(180deg);
      }
    }
  }
}

@media (max-width: 1050px) {
  .stores {
    &__container {
      gap: 20px;
    }
    &__store {
      padding: 20px;
    }
    &__title {
      font-size: 32px;
    }
    &__sub-title {
      font-size: 14px;
    }
  }
}
@media (max-width: 750px) {
  .stores {
    &__container {
      flex-direction: column;
      gap: 20px;
    }
    &__store {
      width: 100%;
    }
    &__ico {
      height: 150px;
    }
    &__buttons {
      display: flex;
      gap: 10px;
      justify-content: space-between;
      align-items: center;
    }
    &__showMoreBtn {
      font-size: 14px;
      letter-spacing: 0.28px;
      &.visible {
        display: block;
        margin: 0 auto;
      }
    }
    &__link {
      margin: 0 auto;
      font-size: 12px;
      width: 134px;
      height: 34px;
      justify-content: space-between;
      padding: 0 15px;
      &::after {
        width: 10px;
        height: 13px;
      }
    }
  }
}
@media (max-width: 490px) {
  .stores {
    &__title {
      font-size: 24px;
    }
    &__sub-title {
      font-size: 12px;
    }
    &__link {
      padding: 0 10px;
    }
    &__showMoreBtn {
      font-size: 12px;
      &::after {
        height: 9px;
        margin-left: 5px;
      }
    }
  }
}
@media (max-width: 375px) {
  .stores {
    &__store {
      padding: 20px 15px;
    }
  }
}
